.Articulos{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: space-around;
    justify-content: space-evenly;
    align-items: center;
    margin: 10px;
    border-radius: 5px;
    background-color: rgb(66, 66, 66);
    width: 250px;
    color: white;
    box-shadow: 5px 5px 6px #00000066;
    height: 400px;
}

.Articulos img{
    width: 150px !important;
    height: 150px !important;
    border-radius: 10px;
}

.contenidoArticulos{
    display: flex;
    flex-direction: column;
    align-content: space-around;
    justify-content: center;
    align-items: center;
    padding: 10px;
    width:200px;
    text-align: center;
    color: white;
}

.contenidoArticulos button{
    margin-top: 5px;
    width: 80px;
    height: 30px;
}

h2{
    margin: 15px !important;
    font-size: 30px;
    text-decoration: none;
}

.contenidoArticulos h3{
    margin: 5px !important;
    text-decoration: none !important;
    display: block;
    font-size: 15px;
}

.quantityButtons{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.quantityButtons :hover{
    background-color: rgb(228, 228, 228) !important;
    color: rgb(66, 66, 66) !important;
}

.countButton{
    margin: 5px;
    display: flex;
    width: 25px !important;
    height: 25px !important;
    justify-content: center;
    font-size: larger;
    align-items: center;
}

.BuyDetailButtons{
    display: flex;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
}

.BuyDetailButtons button{
    margin: 5px;
    border: none;
    border-radius: 3px;
}